.css_navigation_bar {
  position: fixed;
  left: 15px;
  bottom: calc(15px + var(--b-cssvar-safe-area-bottom));
  width: calc(100% - 15px * 2);
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.css_navigation_bar .css_navigation_button_group {
  position: relative;
  height: 45px;
  width: auto;
  display: inline-flex;
  border-radius: 25px;
  background: rgba(var(--d-ffffff-r), var(--d-ffffff-g), var(--d-ffffff-b), var(--d-cssvar-opacity-b));
  border: 1px solid var(--d-cssvar-f5f5f5);
  box-shadow: 0px 0px 17px -10px rgba(0, 0, 0, 0.03);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
}

.css_navigation_bar .css_navigation_button_group .css_navigation_button {
  position: relative;
  width: 45px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.css_navigation_bar .css_navigation_button_group .css_navigation_button span.css_material_symbols_rounded {
  width: 20px;
  height: 20px;
  color: var(--d-cssvar-000000);
}